"use client";

import { useSession } from "next-auth/react";
import { useState } from "react";

interface DesignItem {
  id: string;
  name: string;
}

interface Categories {
  Basic: DesignItem[];
  Knitwear: DesignItem[];
  Activewear: DesignItem[];
}

export default function DesignPage() {
  const { data: session } = useSession();
  const [selectedCategory, setSelectedCategory] = useState<keyof Categories>("Basic");

  const categories: Categories = {
    Basic: [
      { id: "t-shirt", name: "T-Shirt" },
      { id: "hoodie", name: "Hoodie" },
      { id: "crewneck", name: "Crewneck" },
      { id: "long-sleeve", name: "Long Sleeve" },
      { id: "zip-hoodie", name: "Zip-Hoodie" },
      { id: "half-zip", name: "Half Zip" },
    ],
    Knitwear: [
      { id: "knit-hoodie", name: "Knit Hoodie" },
      { id: "knit-crewneck", name: "Knit Crewneck" },
      { id: "knit-scarf", name: "Knit Scarf" },
      { id: "knit-beanie", name: "Knit Beanie" },
    ],
    Activewear: [
      { id: "tech-tshirt", name: "Tech T-Shirt" },
      { id: "tech-long-sleeve", name: "Tech Long Sleeve" },
      { id: "tech-tank", name: "Tech Tank Top" },
      { id: "tech-raglan", name: "Tech T-Shirt Raglan" },
      { id: "tech-long-raglan", name: "Tech Long Sleeve Raglan" },
    ],
  };

  return (
    <main className="flex min-h-screen flex-col bg-[#f9f6f1] p-8">
      <div className="max-w-7xl mx-auto w-full space-y-16">
        <div className="text-center">
          <h1 className="text-4xl font-bold mb-4">What would you like to design?</h1>
          <div className="flex justify-center space-x-4">
            {(Object.keys(categories) as Array<keyof Categories>).map((category) => (
              <button
                key={category}
                onClick={() => setSelectedCategory(category)}
                className={`px-4 py-2 rounded-md ${
                  selectedCategory === category
                    ? "bg-black text-white"
                    : "bg-white text-black border border-gray-200"
                }`}
              >
                {category}
              </button>
            ))}
          </div>
        </div>

        <div className="space-y-12">
          {Object.entries(categories).map(([category, items]) => (
            <div key={category} className="space-y-6">
              <h2 className="text-2xl font-semibold">{category}</h2>
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
                {items.map((item: { id: string; name: string }) => (
                  <div
                    key={item.id}
                    className="bg-white rounded-lg p-6 border border-gray-200 hover:border-black transition-colors cursor-pointer"
                  >
                    <div className="h-40 bg-gray-50 rounded-md flex items-center justify-center mb-4">
                      <svg
                        className="w-24 h-24 text-gray-400"
                        viewBox="0 0 24 24"
                        fill="none"
                        stroke="currentColor"
                        strokeWidth="2"
                      >
                        <rect x="3" y="3" width="18" height="18" rx="2" />
                      </svg>
                    </div>
                    <h3 className="text-lg font-medium text-center">{item.name}</h3>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      </div>
    </main>
  );
}
